import React, {PureComponent} from 'react';
import styles from './LandingPage.module.css';
import Scrambled from 'scramblejs';
import Particles from 'react-particles-js';
import {Transition, animated} from 'react-spring';

const images = [
    'https://firebasestorage.googleapis.com/v0/b/autocure-home.appspot.com/o/vishal1.jpg?alt=media&token=747b843f-1a6f-42f9-af30-8d01871490ea',
    'https://firebasestorage.googleapis.com/v0/b/autocure-home.appspot.com/o/vishal3.jpg?alt=media&token=19a187e8-1a1d-4cc9-9a7f-047e405b9f3a',
    'https://firebasestorage.googleapis.com/v0/b/autocure-home.appspot.com/o/vishal4.jpg?alt=media&token=61df8f00-cfc3-4edd-9584-794e99a6fa1e',
    'https://firebasestorage.googleapis.com/v0/b/autocure-home.appspot.com/o/vishal5.jpg?alt=media&token=ec7242b0-0d02-4018-b9e0-a1839ca514f7',
    'https://firebasestorage.googleapis.com/v0/b/autocure-home.appspot.com/o/vishal6.jpg?alt=media&token=d6dee189-edb2-4809-b114-9ff99288ac8e',
]

let index = 0;

class LandingPage extends PureComponent {

    constructor(props) {
        super(props);
        this.state = {
            image: images[0]
        }
    }

    componentDidMount() {
        const scramble = Scrambled.select('h1');
        this.callScrambler(scramble);
        setInterval(() => this.changeImages(), 3000);
    }

    changeImages = () => {
        if (index === 5) {
            index = 0;
        }
        this.setState({
            image: images[index]
        });
        index++;
    }

    callScrambler = (scramble) => {
        scramble
            .setConfig({flip: 0})
            .createEmpty(0)
            .setText('Google Certified Android Developer')
            .descramble()
            .wait(3000)
            .createEmpty(0)
            .setText('Intel Software Innovator')
            .descramble()
            .wait(3000)
            .setConfig({flip: 2})
            .createEmpty(0)
            .setText('Full Stack Developer')
            .descramble()
            .wait(3000)
            .createEmpty(0)
            .setText('Entrepreneur')
            .descramble()
            .wait(3000)
            .createEmpty(0)
            .setText('Tech Speaker')
            .descramble()
            .wait(3000)
            .createEmpty(0)
            .setText('Mentor')
            .descramble()
            .then(() => setTimeout(() => this.callScrambler(scramble), 3000));
    };

    render() {
        return (
            <div className={styles.container}>
                <Particles
                    className={styles.particles}
                    params={{
                        "particles": {
                            "number": {
                                "value": 100
                            },
                            "size": {
                                "color": "#81c784",
                                "value": 4
                            }
                        },
                        "interactivity": {
                            "events": {
                                "onhover": {
                                    "enable": true,
                                    "mode": "repulse"
                                }
                            }
                        }
                    }}/>
                <div className={styles.scrambleContainer}>
                    <h1></h1>
                </div>
                <div className={styles.picturesContainer}>
                <Transition
                    native
                    items={this.state.image}
                    reset
                    unique
                    from={{ opacity: 0, transform: 'translate3d(100%,0,0)' }}
                    enter={{ opacity: 1, transform: 'translate3d(-10%,0,0)' }}
                    leave={{ opacity: 0, transform: 'translate3d(100%,0,0)' }}
                >
                    {item => props => <animated.img style={props} className={styles['profile-img']} src={item}/>}
                </Transition>
                </div>
            </div>
        )
    }
}

export default LandingPage;