import React, {PureComponent} from 'react';
import styles from './ProjectInfo.module.css';
import {Spring} from 'react-spring';

class ProjectInfo extends PureComponent {

    constructor(props) {
        super(props);
        this.state = {
            animate: false
        }
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
    }

    handleScroll = () => {
        const scroll = window.scrollY;
        if (scroll > 430) {
            this.setState({
                animate: true
            });
        } else if (scroll < 300) {
            this.setState({
                animate: false
            });
        }
    };

    render() {
        const {animate} = this.state;
        return (
            <div id={'reach'} className={styles.container}>
                <div className={styles['data-container']}>
                    <img className={styles.img} src={require('../../images/project.png')}/>
                    <h2 className={styles.h2}>Projects</h2>
                    {animate && <Spring
                        from={{number: 30}}
                        to={{number: 50}}>
                        {props => <h3 className={styles.h3}>{`${props.number.toFixed(0)}+`}</h3>}
                    </Spring>}
                </div>
                <div className={styles['data-container']}>
                    <img className={styles.img} src={require('../../images/presentation.png')}></img>
                    <h2 className={styles.h2}>Talks</h2>
                    {animate && <Spring
                        from={{number: 0}}
                        to={{number: 20}}>
                        {props => <h3 className={styles.h3}>{`${props.number.toFixed(0)}+`}</h3>}
                    </Spring>}
                </div>
                <div className={styles['data-container']}>
                    <img className={styles.img} src={require('../../images/teach.png')}></img>
                    <h2 className={styles.h2}>Students Mentored</h2>
                    {animate && <Spring
                        from={{number: 980}}
                        to={{number: 1000}}>
                        {props => <h3 className={styles.h3}>{`${props.number.toFixed(0)}+`}</h3>}
                    </Spring>}
                </div>
                <div className={styles['data-container']}>
                    <img className={styles.img} src={require('../../images/award.png')}/>
                    <h2 className={styles.h2}>Felicitations</h2>
                    {animate && <Spring
                        immediate={true}
                        from={{number: 0}}
                        to={{number: 10}}>
                        {props => <h3 className={styles.h3}>{`${props.number.toFixed(0)}+`}</h3>}
                    </Spring>}
                </div>
            </div>
        )
    }
}

export default ProjectInfo;